import React from 'react'
import { Layout, Descriptions, Divider, Typography, Empty, Button, message } from 'antd';
import { LightAsync as SyntaxHighlighter } from 'react-syntax-highlighter';
import { anOldHope } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import copy from 'copy-to-clipboard';
import testCode from '../../../static/code/joke.py.code'


export default class CodeDetail extends React.Component {
    copyTo = () => {
        if (copy(testCode)) {
            message.success("复制成功 使用Ctrl+V粘贴")
        } else {
            message.warning("复制失败 您的浏览器不支持自动复制")
        }
    }
    errorFeedback = () =>{
        message.warning("别着急，这个功能还在开发呢~")
    }
    render() {
        return (
            <>
                <Empty
                    style={{ marginTop: '10vh' }}
                    hidden={this.props.id !== -1 ? true : false}
                    image={Empty.PRESENTED_IMAGE_SIMPLE}
                    description="请选择模块"
                />
                <Layout.Content
                    style={{ margin: '16px' }}
                    hidden={this.props.id === -1 ? true : false}
                >
                    <Descriptions
                        title="XXXX加密模块"
                        size="small"
                    >
                        <Descriptions.Item label="作者"><Typography.Link>@ThCode</Typography.Link></Descriptions.Item>
                        <Descriptions.Item label="日期">2021/02/21</Descriptions.Item>
                        <Descriptions.Item label="版本">1.0.0</Descriptions.Item>
                        <Descriptions.Item label="引用"><Typography.Link>MD5Encrypt</Typography.Link>、<Typography.Link>RSAUtil</Typography.Link></Descriptions.Item>
                        <Descriptions.Item label="转载"><Typography.Link>首次上传</Typography.Link></Descriptions.Item>
                        <Descriptions.Item label="编号"><Typography.Link>{this.props.id}</Typography.Link></Descriptions.Item>

                    </Descriptions>
                    <Divider />
                    <Descriptions
                        bordered
                        size="small"
                        column={1}
                    >
                        <Descriptions.Item label="使用说明">
                            导入xxx模块：<Typography.Link>模块xxx</Typography.Link><br />
                        安装方法：……
                        </Descriptions.Item>
                        <Descriptions.Item label="前置模块">
                            无
                        </Descriptions.Item>
                        <Descriptions.Item label="操作">
                            <Button type="primary" style={{ marginRight: 3 }} onClick={this.copyTo}>复制到剪贴板</Button>
                            <Button type="danger" onClick={this.errorFeedback}>错误反馈</Button>
                        </Descriptions.Item>
                    </Descriptions>
                    <SyntaxHighlighter language="python" style={anOldHope}>
                        {testCode}
                    </SyntaxHighlighter>

                </Layout.Content>
            </>
        )
    }
}